<template>
  <v-container fluid style="height: 500px; width: 100% d-flex justify-center align-center">
    <div style="height: 200px overflow: auto;">
      <p>First marker is placed at {{ withPopup.lat }}, {{ withPopup.lng }}</p>
      <p>Center is at {{ currentCenter }} and the zoom is: {{ currentZoom }}</p>
      <v-btn @click="showLongText" class="ma-4"> Toggle long popup </v-btn>
      <v-btn @click="showMap = !showMap"> Toggle map </v-btn>
      <v-text-field label="search location" @keyup="search()" v-model="searchData" type="text" class="px-6"></v-text-field>
      
      <v-card v-for="(i,index) in Result" :key="index" @click="location(index)" ref="searchResult" class="px-6">{{i.label}}}</v-card>
    </div>
    <l-map
      v-if="showMap"
      :zoom="zoom"
      :center="center()"
      :options="mapOptions"
      style="height: 80%"
      @update:center="centerUpdate"
      @update:zoom="zoomUpdate"
    >
      <l-tile-layer :url="url" :attribution="attribution" />
      <l-marker v-for="(x,index) in markers" :key="index" :lat-lng="x">
        <l-popup>
          <div @click="innerClick">
            I am a popup
            <p v-show="showParagraph">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Quisque
              sed pretium nisl, ut sagittis sapien. Sed vel sollicitudin nisi.
              Donec finibus semper metus id malesuada.
            </p>
          </div>
        </l-popup>
      </l-marker>
    </l-map>
  </v-container>
</template>

<script>
import { latLng } from "leaflet";
import { OpenStreetMapProvider } from "leaflet-geosearch";
import { LMap, LTileLayer, LMarker, LPopup } from "vue2-leaflet";

export default {
  name: "Example",
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
  },
  data() {
    return {
      markers: [],
      searchData: "",
      Result: 0,
      zoom: 13,
      lat: 0,
      lng: 0,
      //center: latLng(47.41332, -1.219482),
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution:
        '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      //withPopup: latLng(47.41322, -1.219482),
      //withTooltip: latLng(47.41422, -1.250482),
      currentZoom: 11.5,
      //currentCenter: latLng(this.lat, this.lng),
      showParagraph: false,
      mapOptions: {
        zoomSnap: 0.5,
      },
      showMap: true,
    };
  },
  computed: {
    dynamicSize() {
      return [this.iconSize, this.iconSize * 1.15];
    },
    dynamicAnchor() {
      return [this.iconSize / 2, this.iconSize * 1.15];
    },
  },
  methods: {
    zoomUpdate(zoom) {
      this.currentZoom = zoom;
    },
    centerUpdate(center) {
      this.currentCenter = center;
    },
    showLongText() {
      this.showParagraph = !this.showParagraph;
    },
    innerClick() {
      alert("Click!");
    },
    currentCenter() {
      return latLng(this.lat, this.lng);
    },
    center() {
      return latLng(this.lat, this.lng);
    },
    withPopup() {
      return latLng(this.lat, this.lng);
    },
    withTooltip() {
      return latLng(this.lat + 1, this.lng - 1);
    },
    location(i){
      this.markers.push(latLng(this.Result[i].y, this.Result[i].x))
      this.lat = this.Result[i].y;
      this.lng = this.Result[i].x;
      this.zoom = 18
      console.log(this.Result[i].x, this.Result[i].y)
      console.log(this.markers)
    },
    async search() {
      event.preventDefault();
      const provider = new OpenStreetMapProvider();
      this.Result = await provider.search({ query: this.searchData });
      await console.log(this.Result);
      console.log(this.Result);
      this.$refs.searchResult.innerHTML = this.Result[0].label

    }
  },
  created() {
    this.$getLocation({})
      .then((coordinates) => {
        this.lat = coordinates.lat;
        this.lng = coordinates.lng;
        console.log(this.lat, this.lng);
      })
      .catch((error) => alert(error));

  },
};
</script>
